    border-radius: 10px;
    /* padding: 10px; */
    background: #fdf9fd;
    box-shadow: 5px 6px #313131;
}

.profile .moodboard {
    width: 100%; 
    /* min-height: 100%;  */
    /* object-fit: cover; */
    display: block;
    /* border-radius: 15px; */
    grid-row: 2;
}

/* ROW 1: bio */
.profile .bio {
    display: -webkit-flex;
    display: flex;
    grid-row: 1;
    padding: 20px 15px;
    overflow: hidden;
}

.profile .profilepic {
    grid-row: 1;
    border: 2px solid black;
    border-radius: 20%;
    height: 4.5em;
    width: 4.5em;
    object-fit: cover;
    overflow: hidden;
    background: black;   
}

.profile .profilepic > img {
    /* display: inline; */
    /* margin: -5% -35%; */
    height: 110%;
    width: auto;
    background: black;
}

.profile .bio-button {
    /* width: 80%; */
    -webkit-align-self: right;
            align-self: right;
}

.profile .bio-text {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 15px;
    -webkit-justify-content: center;
            justify-content: center;
}

.profile h2 {
    font-weight: 900;
}

.profile h3 {
    color: #5c5c5c;
    font-weight: 500;
    font-size: 1em;
    padding-top: 3px;
}

.profile p {
    color: #7c7c7c;
    font-size: 0.8em;
    font-weight: 300;
    padding-top: 3px;
}

/* ROW 3 - action bar */
