    display: -webkit-flex;
    display: flex;
    /* grid-template-columns: 2fr 3fr; */
    /* grid-column-gap: 20px; */
    -webkit-justify-content: space-between;
            justify-content: space-between;
    /* background: #f4e4f7; */
    /* grid-row: 3; */
    padding: 15px 12px 10px;
}

.profile a {
    color: black;
}

.profile .social-links {
    display: -webkit-flex;
